.border{
  border: 2px solid black;
  border-radius: 20px;
  width: 30%;
  margin: 0 auto;
  height: 30px;
  overflow: hidden;
  
  
}

.body{
  display: flex;
  height: 100vh;
  justify-content: center;
  flex-direction: column;
}

.progres{
  width: 0;
  background-color: red;
  height: 100%;
  display: flex;
  align-items: center;
  border-radius: 30px;
}

.ClassForinput{
  display: flex;
  justify-content: center;
  align-items: center;
}

.inpForValue{
  width: 10%;
  display: block;
  border-radius: 50px;
  padding: 20px 5px;
  font-size: 25px;
  color: blue;
  font-family: fantasy;
}

.ClassForinput {
  margin-top: 15px;
}

.ClassForinput p {
  margin-right: 10px;
}

.P_border{
  text-align: center;
  margin: 0 auto;
  color: white;
  font-family: fantasy;
  color: blue;
  font-size: 20px;
  
}


@media(max-width: 600px) {
  .border{
    
    width: 60%;
    
    
    
  }

  .inpForValue {
    width: 14%;
    
}
  
}